
import { GCreateOrderModalForm, GDetailOrderModalShield } from '@/components';
import { mapActions } from 'vuex';

export default {
	name: 'MainLayout',
	components: { GCreateOrderModalForm, GDetailOrderModalShield },
	data: () => ({
		links: [
			{ id: 0, title: '', url: '/' },
			{ id: 1, title: '', url: '/stock' },
			{ id: 2, title: '', url: '/orders' },
			{ id: 3, title: '', url: '/clients' },
			{ id: 4, title: '', url: '/feedbacks' },
			{ id: 5, title: '', url: '/groomers' },
		],
		logoIcon: require('../assets/logo.svg'),
		logoutIcon: require('../assets/svg/logout.svg'),
	}),
	mounted() {
		this.GET_SERVICES();
		this.GET_ALL_GROOMERS();
		this.GET_ALL_CLIENTS();
		this.GET_ALL_PRODUCTS();
	},
	methods: {
		...mapActions([
			'LOGOUT',
			'GET_SERVICES',
			'GET_ALL_GROOMERS',
			'GET_ALL_CLIENTS',
			'GET_ALL_PRODUCTS',
		]),
		async logout() {
			const { status } = await this.LOGOUT();

			if (status === 'success') {
				this.$router.push({name: 'g-login'});
			}
		},
	},
};
