<template>
	<div class="g-feedbacks flex-grow-1 d-flex flex-column">
		<h1></h1>
		<div class="g-feedbacks-table flex-grow-1 d-flex flex-column">
			<v-data-table
				class="flex-grow-1"
				:items-per-page="itemsPerPage"
				:headers="headers"
				:items="FEEDBACKS"
				no-data-text="   "
				no-results-text="   "
				loading-text=""
				hide-default-footer
				:page="currentPage"
				@pagination="setCurrentPage"
			>
				<template v-slot:item="{ item }">
					<tr>
						<td class="text-left">
							<span>{{ item.nick }}</span>
						</td>
						<td class="text-left">
							<span>{{ item.text }}</span>
						</td>
						<td class="text-center">
							<div class="d-flex justify-center">
								<v-checkbox
									:input-value="item.isApproved"
									color="#FFC11C"
									hide-details
									class="mt-0"
									@change="v => changeFeedback(item.id, v)"
								/>
							</div>
						</td>
					</tr>
				</template>
				<template v-slot:footer="{ props: { options, pagination } }">
				<div class="v-data-footer__wrapper d-flex align-center pl-2">
					<div class="v-data-footer__info">
						: {{FEEDBACKS.length}} {{declOfNum(FEEDBACKS.length, titles)}}
					</div>
					<v-data-footer
						class="flex-grow-1"
						:options="options"
						:pagination="pagination"
						items-per-page-text="  :"
						items-per-page-all-text=""
						show-current-page
						show-first-last-page
						:page-text="`${currentPage}  ${pageCount}`"
						@update:options="footerUpdate"
					/>
				</div>
			</template>
			</v-data-table>
		</div>
	</div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import { declOfNum } from '@/services';

export default {
	name: 'GFeedbacks',
	data: () => ({
		currentPage: 0,
		pageCount: 0,
		itemsPerPage: 15,
		headers: [
			{ text: ' ', value: 'nick', width: 300 },
			{ text: '', value: 'text' },
			{
				text: '',
				value: 'isApproved',
				width: 150,
				align: 'center',
			},
		],
		titles: ['', '', '']
	}),
	computed: {
		...mapGetters(['FEEDBACKS']),
	},
	mounted() {
		this.GET_ALL_FEEDBACKS();
	},
	methods: {
		...mapActions(['GET_ALL_FEEDBACKS', 'UPDATE_FEEDBACK']),
		declOfNum,
		async changeFeedback(id, isApproved) {
			const res = await this.UPDATE_FEEDBACK({id, data: {isApproved}})
			console.log('res', res)
		},
		setCurrentPage({ page, pageCount }) {
			this.currentPage = page;
			this.pageCount = pageCount;
		},
		footerUpdate({page, itemsPerPage}) {
			this.currentPage = page
			this.itemsPerPage = itemsPerPage
		},
	},
};
</script>

