
import { onPhoneInput } from '@/utils/phoneMask';
import GColorSelector from './GColorSelector.vue';
import { defaultColors } from '@/variables';

export default {
	name: 'GCreateGroomerModalForm',
	components: { GColorSelector },
	props: {
		isAvtive: {
			type: Boolean,
			default: false,
		},
	},
	data: () => ({
		...defaultData,
		username: '',
		lastname: '',
		phone: '',
		post: 'groommer',
		education: '',
		address: '',
		rate: 0,
		comment: '',
		posts: [
			{ text: ' ', value: 'main_groommer' },
			{ text: '', value: 'groommer' },
			{ text: ' ', value: 'helper' },
		],
		color: '#FFBE11',
		colors: defaultColors,
	}),
	computed: {
		isFormValid() {
			return !!this.username;
		},
		form() {
			return {
				username: this.username.trim(),
				lastname: this.lastname.trim(),
				phone: this.phone.trim(),
				comment: this.comment.trim(),
				post: this.post.toLowerCase(),
				color: this.color.trim(),
				education: this.education.trim(),
				address: this.address.trim(),
				rate: this.rate,
			};
		},
	},
	methods: {
		closeModal() {
			this.clearFrom();
			this.$emit('onModalClose');
		},
		clearFrom() {
			Object.keys(defaultData).forEach(key => {
				this[key] = defaultData[key];
			});
		},
		submitForm() {
			if (!this.isFormValid) return;

			this.$emit('onSubmit', this.form);
			this.closeModal();
		},
		onKeyDown({ keyCode }: { keyCode: number }) {
			// on Escape press
			if (keyCode === 27) {
				this.closeModal();
			}
		},
		setRate(v: string) {
			const value = v.replace(/\D/g, '');

			if (this.rate === +value) {
				this.rate = v;
			}

			setTimeout(() => (this.rate = +value), 0);
		},
		setPhone(v: string) {
			const value = onPhoneInput(v);

			if (this.phone === value) {
				this.phone = v;
			}

			setTimeout(() => (this.phone = value), 0);
		},
	},
};

const defaultData: any = {
	username: '',
	lastname: '',
	post: 'groommer',
	phone: '',
	comment: '',
	color: '#FFC11C',
	rate: 0,
	address: '',
};
