<template>
	<div class="g-orders flex-grow-1 d-flex flex-column">
		<h1 class="mb-4"></h1>
		<v-divider class="mb-5" />
		<v-row class="flex-grow-0 my-0 mb-5">
			<v-col cols="3" class="py-0 d-flex align-end">
				<!-- <v-text-field
					v-model="searchStr"
					prepend-inner-icon="mdi-magnify"
					label=" "
					single-line
					hide-details
					color="#FFC11C"
					filled
					dense
				/> -->
			</v-col>
			<v-col class="d-flex py-0">
				<v-spacer />
				<v-btn x-large tile outlined elevation="0" @click="createOrder">
					<v-icon>mdi-plus</v-icon>
					 
				</v-btn>
			</v-col>
		</v-row>
		<v-data-table
			class="flex-grow-1"
			:items-per-page="itemsPerPage"
			:headers="headers"
			:items="currentOrders"
			no-data-text="   "
			no-results-text="   "
			loading-text=""
			hide-default-footer
			:page="currentPage"
			@pagination="setCurrentPage"
		>
			<template v-slot:item="{ item }">
				<tr class="pointer" @click="showDetail(item)">
					<td class="py-2">
						{{ item.id }}
					</td>
					<td class="text-xs-right py-2">
						{{ item.createDate }}
					</td>
					<td class="text-xs-right py-2" :class="`is-${item.status}`">
						{{ statuses[item.status] }}
					</td>
					<td class="text-xs-right py-2">
						{{ item.price }}
					</td>
				</tr>
			</template>
			<template v-slot:footer="{ props: { options, pagination } }">
				<div class="v-data-footer__wrapper d-flex align-center pl-2">
					<div class="v-data-footer__info">
						: {{currentOrders.length}} {{declOfNum(currentOrders.length, titles)}}
					</div>
					<v-data-footer
						class="flex-grow-1"
						:options="options"
						:pagination="pagination"
						items-per-page-text="  :"
						items-per-page-all-text=""
						show-current-page
						show-first-last-page
						:page-text="`${currentPage}  ${pageCount}`"
						@update:options="footerUpdate"
					/>
				</div>
			</template>
		</v-data-table>
	</div>
</template>

<script>
import { declOfNum } from '@/services';
import { mapActions, mapMutations, mapGetters } from 'vuex';

const dateFormatter = date => {
	return new Intl.DateTimeFormat('ru-RU', {
		// year: 'numeric',
		// month: 'long',
		// day: 'numeric',
		dateStyle: 'medium',
		timeStyle: 'short'
	}).format(new Date(date));
};

export default {
	name: 'GOrders',
	data: () => ({
		searchStr: '',
		headers: [
			{ text: ' ', value: 'id' },
			{ text: ' ', value: 'createDate' },
			{ text: ' ', value: 'status' },
			{ text: ' ', value: 'price' },
		],
		orders: [],
		currentPage: 0,
		pageCount: 0,
		itemsPerPage: 15,
		statuses: {
			reserved: '',
			success: '',
			cancel: '',
			overdue: '',
		},
		titles: ['', '', '']
	}),
	computed: {
		...mapGetters(['ALL_ORDERS']),
		currentOrders() {
			return this.orders.map(order => {
				let status = 'reserved';

				if (order.isCancel) status = 'cancel';
				else if (order.isSuccess) status = 'success';

				return {
					id: order.id,
					createDate: dateFormatter(order.createDate),
					price: order.price,
					status,
					data: order,
				};
			});
		},
	},
	watch: {
		ALL_ORDERS(orders) {
			this.orders = orders || []
		},
	},
	mounted() {
		this.GET_ALL_ORDERS()
	},
	methods: {
		...mapActions(['GET_ALL_ORDERS']),
		...mapMutations(['SHOW_ORDER_FORM', 'SHOW_ORDER_DETAIL_SHIELD']),
		declOfNum,
		createOrder() {
			this.SHOW_ORDER_FORM();
		},
		setCurrentPage({ page, pageCount }) {
			this.currentPage = page;
			this.pageCount = pageCount;
		},
		showDetail({ data: order }) {
			this.SHOW_ORDER_DETAIL_SHIELD(order)
		},
		footerUpdate({page, itemsPerPage}) {
			this.currentPage = page
			this.itemsPerPage = itemsPerPage
		},
	},
};
</script>

<style lang="scss">
.g-orders {
	td {
		&.is-reserved {
			color: #72c565;
		}
		&.is-success {
			color: rgba(36, 49, 56, 0.38);
		}
		&.is-cancel {
			color: #ff5252;
		}
	}
}
</style>
